<template>
  <div class="product-container">
    <Search @searchSubmit="searchSubmit" :categoryList="categoryList" />
    <a-button class="add-product" @click="openAddProduct">添加商品</a-button>
    <ProductTable
      :data="data"
      :page="page"
      @changePage="changePage"
      @editProduct="openEditProduct"
      @removeProduct="removeProduct"
    />
    <a-modal
      v-model="visible"
      :title="dialogTitle"
      :ok-text="okText"
      cancel-text="取消"
      @ok="submitProduct"
      width="650px"
    >
      <a-form-model
        ref="productForm"
        :model="productForm"
        :rules="productRules"
        layout="horizontal"
        :label-col="labelCol"
        :wrapper-col="wrapperCol"
      >
        <a-form-model-item label="商品标题" prop="title">
          <a-input v-model="productForm.title" />
        </a-form-model-item>
        <a-form-model-item label="商品描述">
          <a-input v-model="productForm.desc" type="textarea" />
        </a-form-model-item>
        <a-form-model-item label="商品类目" prop="category">
          <a-select placeholder="请选择商品类目" v-model="productForm.category">
            <a-select-option :value="c.id" v-for="c in categoryList" :key="c.id">{{c.name}}</a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="商品标签" required>
          <a-input v-model="tag">
            <a-icon slot="suffix" type="plus-circle" @click="addTag" />
          </a-input>
          <span
            title="点击删除"
            @click="removeTag(i)"
            :style="tagStyle"
            v-for="(t,i) in productForm.tags"
            :key="i"
          >{{t}}</span>
        </a-form-model-item>
        <a-form-model-item label="商品价格" prop="price">
          <a-input-number v-model="productForm.price" :min="0" />
        </a-form-model-item>
        <a-form-model-item label="商品折扣价" prop="price_off">
          <a-input-number v-model="productForm.price_off" :min="0" />
        </a-form-model-item>
        <a-form-model-item label="商品库存量" prop="inventory">
          <a-input-number v-model="productForm.inventory" :min="0" />
        </a-form-model-item>
        <a-form-model-item label="商品单位" prop="unit">
          <a-input v-model="productForm.unit" />
        </a-form-model-item>
        <a-form-model-item label="是否上架" >
          <a-switch @change="onChange" v-model="defaultChecked" />
        </a-form-model-item>
        <a-form-item label="图片">
          <a-upload
            :action="`https://mallapi.duyiedu.com/upload/images?appkey=${$store.state.user.user.appkey}`"
            list-type="picture-card"
            :file-list="fileList"
            name="avatar"
            @change="changeImage"
          >
            <div v-if="fileList.length < 8">
              <a-icon type="plus" />
              <div class="ant-upload-text">Upload</div>
            </div>
          </a-upload>
        </a-form-item>
      </a-form-model>
    </a-modal>
  </div>
</template>

<script>
import Search from "@/components/search.vue";
import ProductTable from "@/components/productTable.vue";
import productApi from "@/api/product";
import categoryApi from "@/api/category";

export default {
  data() {
    return {
      data: [], // 表格数据
      page: {
        current: 1,
        total: 1,
        pageSize: 10,
        showSizeChanger: true,
      },
      labelCol: { span: 6 },
      wrapperCol: { span: 14 },
      searchForm: {}, // 检索表单
      categoryList: [], // 类目列表
      visible: false, // 弹框可见
      dialogTitle: "", // 弹框标题
      okText: "", // 弹框底部确认文字
      dialogStatus: "", // add-添加商品,edit-修改商品
      tag: "", // 标签内容
      defaultChecked: false, // 编辑界面状态默认是否上架
      tagStyle: {
        // 标签样式
        border: "1px solid #ccc",
        padding: "5px",
        marginRight: "5px",
        borderRadius: "5px",
        cursor: "pointer",
      },
      productForm: {
        title: "", // 标题
        desc: "", // 描述
        category: "", // 类目
        tags: [], // 标签
        price: 0, // 价格
        price_off: 0, // 折扣价
        unit: "", // 单位
        status: 0, // 上架状态 0-下架 1-上架
        images: [], // 图片
        inventory: 0, // 库存量
        sale: 0, // 销量
      },
      productRules: {
        title: [{ required: true, message: "请输入商品标题", trigger: "blur" }],
        category: [
          { required: true, message: "请选择商品类目", trigger: "change" },
        ],
        price: [{ required: true, message: "请输入商品价格", trigger: "blur" }],
        price_off: [
          { required: true, message: "请输入商品折扣价", trigger: "blur" },
        ],
        unit: [{ required: true, message: "请输入商品单位", trigger: "blur" }],
        inventory: [
          { required: true, message: "请输入商品库存量", trigger: "blur" },
        ],
      },
      fileList: [],
    };
  },
  components: {
    Search,
    ProductTable,
  },
  created() {
    categoryApi.get().then((res) => {
      this.categoryList = res.data.data;
    });
    this.getTableData();
  },
  methods: {
    // 搜索按钮触发
    searchSubmit(params) {
      this.searchForm = params;
      this.page.current = 1;
      this.getTableData();
    },
    // 获取商品数据
    async getTableData() {
      const res = await productApi.get({
        page: this.page.current,
        size: this.page.size,
        ...this.searchForm,
      });
      // 修改商品类目对应名称
      this.data = res.data.data.map((item,i) => {
        const obj = item;
        this.categoryList.forEach((c) => {
          if (obj.category === c.id) {
            obj.categoryName = c.name;
          }
        });
        return obj;
      });
      this.page.total = res.data.total;
    },
    // 页码修改
    changePage(page) {
      this.page = page;
      this.getTableData();
    },
    // 打开添加商品弹框
    openAddProduct() {
      this.clearProductForm();
      this.dialogTitle = "添加商品";
      this.okText = "添加";
      this.dialogStatus = "add";
      this.visible = true;
    },
    // 添加/修改商品
    submitProduct() {
      this.$refs.productForm.validate((valid) => {
        if (valid) {
          if (this.dialogStatus === "add") {
            // 添加商品
            productApi.add(this.productForm).then((res) => {
              if (res.status === "success") {
                // 添加成功
                this.$message.success("添加成功");
                this.visible = false;
                this.getTableData();
              }
            });
          } else if (this.dialogStatus === "edit") {
            // 编辑商品
            productApi.edit(this.productForm).then((res) => {
              if (res.status === "success") {
                // 修改成功
                this.$message.success("修改成功");
                this.visible = false;
                this.getTableData();
              }
            });
          }
        }
      });
    },
    // 打开编辑商品
    openEditProduct(pro) {
      this.dialogStatus = "edit"; // 弹框为编辑
      this.dialogTitle = "编辑商品"; // 设置弹框标题
      this.okText = "修改"; // 设置弹框确定文字
      this.defaultChecked = pro.status === 1 ? true : false;
      this.productForm = {
        title: pro.title, // 标题
        desc: pro.desc, // 描述
        category: pro.category, // 类目
        tags: pro.tags, // 标签
        price: pro.price, // 价格
        price_off: pro.price_off, // 折扣价
        unit: pro.unit, // 单位
        status: pro.status, // 上架状态 0-下架 1-上架
        images: pro.images, // 图片
        inventory: pro.inventory, // 库存量
        sale: pro.sale, // 销量
        _id: pro._id,
        id: pro.id, 
      };
      this.fileList = this.productForm.images.map((img, i) => {
        return {
          uid: i,
          name: "image" + i,
          status: "done",
          url: img,
        };
      });
      this.visible = true; // 打开弹框
    },
    // 删除商品
    removeProduct(pro) {
      const that = this;
      this.$confirm({
        title: "确定删除该商品吗",
        okText: "确定",
        cancelText: "取消",
        onOk() {
          productApi.remove(pro.id).then((res) => {
            if (res.status === "success") {
              // 删除成功
              that.$message.success("删除成功");
              that.getTableData();
            }
          });
        },
      });
    },
    // 添加标签
    addTag() {
      if (this.tag.length < 1) {
        return;
      }
      this.productForm.tags.push(this.tag);
      this.tag = "";
    },
    // 删除标签
    removeTag(i) {
      this.productForm.tags = this.productForm.tags.filter(
        (ele, index) => i !== index
      );
    },
    // 上架状态修改
    onChange(status) {
      if (status) {
        this.productForm.status = 1;
      } else {
        this.productForm.status = 0;
      }
    },
    // 清空表单
    clearProductForm() {
      this.productForm = {
        title: "", // 标题
        desc: "", // 描述
        category: "", // 类目
        tags: [], // 标签
        price: 0, // 价格
        price_off: 0, // 折扣价
        unit: "", // 单位
        status: 0, // 上架状态 0-下架 1-上架
        images: [], // 图片
        inventory: 0, // 库存量
        sale: 0, // 销量
      };
      this.fileList = []; // 清空表单图片
      this.defaultChecked = false; // 默认不上架
    },
    changeImage({ fileList, file }) {
      if (file.status === "done") {
        this.productForm.images.push(file.response.data.url);
      } else if (file.status === "removed") {
        const url = file.url
        this.productForm.images = this.productForm.images.filter(
          (item) => {
            return item !== url
          }
        );
      }
      this.fileList = fileList;
    },
  },
};
</script>

<style lang="less" scoped>
.product-container {
  .add-product {
    float: right;
    margin-right: 10px;
    margin-top: 12px;
  }
}
</style>
