import axs from '@/axios';

export default {
  // 获取类目列表
  get() {
    return axs.get('/category/all');
  },
  add(params) {
    return axs.post('/category/add', params);
  },
  edit(params) {
    return axs.put('/category/edit', params);
  },
  remove(id) {
    return axs.delete(`/category/${id}`);
  },
};
