<template>
  <div class="product-table-container">
    <a-table
      :columns="columns"
      :data-source="data"
      :pagination="page"
      @change="changePage"
      row-key="_id"
      :scroll="{
          y: 'calc(100vh - 230px)'
      }"
    >
      <div slot="image" slot-scope="text, record">
        <img :src="record.images[0] ? record.images[0] : require('@/assets/images/default.png')" width="90px" height="90px" alt="">
      </div>
      <div slot="tags" slot-scope="text, record">
        <span style="margin-right:10px" v-for="(t,i) in record.tags" :key="i">
          {{'【'+t+ '】'}}
        </span>
      </div>
      <div slot="status" slot-scope="text, record">
        {{record.status === 1 ? '已上架' : '已下架'}}
      </div>
      <div slot="operation" slot-scope="text, record">
        <a-button @click="editProduct(record)">编辑</a-button>
        <a-button @click="removeProduct(record)">删除</a-button>
      </div>
    </a-table>
  </div>
</template>
<script>
const columns = [
  {
    title: '图片',
    scopedSlots: { customRender: 'image' },
  },
  {
    dataIndex: 'title',
    title: '标题',
    ellipsis: true,
  },
  {
    title: '描述',
    dataIndex: 'desc',
    ellipsis: true,
  },
  {
    title: '类目',
    dataIndex: 'categoryName',
  },
  {
    title: '预售价',
    dataIndex: 'price',
  },
  {
    title: '折扣价',
    dataIndex: 'price_off',
  },
  {
    title: '库存',
    dataIndex: 'inventory',
  },
  {
    title: '标签',
    scopedSlots: { customRender: 'tags' }
  },
  {
    title: '上架状态',
    scopedSlots: { customRender: 'status' },
  },
  {
    title: '操作',
    scopedSlots: { customRender: 'operation' },
    width: 200,
  },
];
export default {
  props: ['data', 'page'],
  data() {
    return {
      columns,
    };
  },
  methods: {
    changePage(page) {
      this.$emit('changePage', page);
    },
    // 编辑产品
    editProduct(record) {
      this.$emit('editProduct', record);
    },
    // 删除产品
    removeProduct(record) {
      this.$emit('removeProduct', record);
    },
  },
};
</script>
