<template>
  <div class="search-container">
    <a-form layout="inline" :form="searchForm" @submit="handleSubmit">
      <a-form-item label="搜索关键字：">
        <a-input v-model="searchForm.searchWord" placeholder="请输入关键字"></a-input>
      </a-form-item>
      <a-form-item label="商品类目：">
        <a-select
          show-search
          placeholder="请选择类目"
          option-filter-prop="children"
          style="width: 200px"
          :filter-option="filterOption"
          allowClear
          @change="handleChange"
        >
          <a-select-option :value="c.id" v-for="c in categoryList" :key="c.id">
              {{c.name}}
          </a-select-option>
        </a-select>
      </a-form-item>
      <a-form-item>
        <a-button type="primary" html-type="submit">搜索</a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>
export default {
  props: ['categoryList'],
  data() {
    return {
      user: this.$store.state.user.user,
      searchForm: {
        searchWord: '',
        category: '',
      },
    };
  },
  methods: {
    // 下拉框选择
    handleChange(val) {
      this.searchForm.category = val;
    },
    // 表单提交
    handleSubmit() {
      this.$emit('searchSubmit', this.searchForm);
    },
    // 选择框输入筛选
    filterOption(input, option) {
      return (
        option.componentOptions.children[0].text
          .toLowerCase()
          .indexOf(input.toLowerCase()) >= 0
      );
    },
  },
};
</script>
<style lang="less" scoped>
    .search-container{
      display: inline-block;
        padding: 10px 20px;
    }
</style>
