import axs from '@/axios';

export default {

  // 获取商品列表
  get(params) {
    return axs.get('/products/all', { params });
  },
  // 新增商品
  add(params) {
    return axs.post('/products/add', params);
  },
  // 编辑商品
  edit(params) {
    return axs.put('/products/edit', params);
  },
  // 删除商品
  remove(id) {
    return axs.delete(`/products/${id}`);
  },
};
